/**
     * Случайная строка заданной длины или формата
     * Принимает произвольное количество параметров
     * Параметры типа int заменяются случайными строками соотв. длины
     * Остальные параметры приводятся к строке
     * Возвращается конкатенация преобразованных параметров
     */
function rStr() {
    return Array.prototype.reduce.call(arguments, function (a, b) {
        if (typeof b == 'number' && b > 0) {
            return a + ((Math.random() * 14 + 1) * Math.pow(16, b - 1)).toString(16).split('.')[0];
        } else if (typeof b == 'number' || b === undefined || b === null) {
            return a;
        } else {
            return a + b.toString();
        }
    }, '');
}
/**
     * Случайное целое или дробное число в заданном диапазоне
     * @param {bool} int - округлять ли число до целого
     * @param {int} min
     * @param {int} max
     */
function rNum(int, min, max) {
    var rnd = Math.random();
    var num;
    if (max >= min) {
        num = rnd * (max - min) + min;
    } else if (max < min) {
        num = rnd * (min - max) + max;
    } else {
        throw new TypeError();
    }
    if (int) {
        num = Math.floor(num);
    }
    return num;
}

const random = {
    str: rStr,
    int: rNum.bind(null, true),
    num: rNum.bind(null, false)
};
export default random;
