import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import GearIcon from './icons/GearIcon';
import './style/index.less';

const FormFrame = ({ pending, children, blockName, className }) => (
    <div className={classNames(blockName, className)}>
        {React.Children.map(children, (child) => {
            if (typeof child === 'string') {
                return <span className={`${blockName}__text`}>{child}</span>;
            }
            if (React.isValidElement(child)) {
                return React.cloneElement(child, { framed: false });
            } else return child;
        })}
        {pending ? (
            <span className={`${blockName}__preloader`}>
                {/* <i className="pro-icon pro-icon--spin"> */}
                <GearIcon className="pro-icon pro-icon--spin" />
                {/* </i> */}
            </span>
        ) : null}
    </div>
);

FormFrame.propTypes = {
    /**
     * Показывать ли прелоадер
     */
    pending: PropTypes.bool,
    /**
     * Базовый CSS-класс
     */
    blockName: PropTypes.string,
    /**
     * Дополнительный CSS-класс
     */
    className: PropTypes.string,
    /**
     * Дочерние элементы обрабатываются в зависимости от типа:
     * - строки оборачиваются в pro-form__frame__text
     * - элементы копируются и получают свойство framed: false (для снятия границ)
     * - остальные объекты выводятся как есть
     */
    children: PropTypes.any
};

FormFrame.defaultProps = {
    pending: false,
    blockName: 'pro-form__frame',
    className: '',
    children: null
};

export default FormFrame;
