import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import random from '../../utils/random';

export default function SVG({
    id,
    title,
    children,
    className,
    animationName,
    ...props
}) {
    // ID заголовка, необходим для ссылки из ARIA-атрибута
    const tid = (id || `icon_${random.str(5)}`) + '_title';
    const resultClassName = className != null ? className : 'pro-icon';
    return (
        <i
            className={classNames(
                resultClassName,
                animationName ? `${resultClassName}--${animationName}` : ''
            )}
        >
            <svg
                id={id}
                version="1.1"
                baseProfile="full"
                xmlns="http://www.w3.org/2000/svg"
                shapeRendering="geometricPrecision"
                aria-labelledby={tid}
                {...props}
            >
                <title id={tid}>{title}</title>
                {children}
            </svg>
        </i>
    );
}

SVG.propTypes = {
    id: PropTypes.string,
    title: PropTypes.string,
    children: PropTypes.oneOfType([
        PropTypes.element,
        PropTypes.arrayOf(PropTypes.element)
    ]),
    className: PropTypes.string,
    animationName: PropTypes.oneOf(['spin'])
};
